import React,{useState} from 'react'
import * as FaIcons from "react-icons/fa"; //npm install react-icons
import * as AiIcons from 'react-icons/ai';
import {NavabarData} from './NavbarData.js';
import * as MatIcons from '@material-ui/icons';
// import {SearchBar} from "material-ui-search-bar";
// import InputBase from '@material-ui/core/InputBase';
import * as Material from '@material-ui/core'
import './Navbar.css';
import { fade, makeStyles } from '@material-ui/core/styles';
// import {PrimarySearchAppBar} from './PrimarySearchAppBar.js'
import{IconContext} from 'react-icons'
import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(10),
    },
   
    search: {
      position: 'relative',
      borderRadius: theme.shape.borderRadius,
      backgroundColor: fade(theme.palette.common.white, 0.15),
      '&:hover': {
        backgroundColor: fade(theme.palette.common.white, 0.25),
      },
      marginLeft: 0,
      width: '100%',
      [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(1),
        width: 'auto',
      },
    },
    searchIcon: {
      padding: theme.spacing(0, 2),
      height: '100%',
      position: 'absolute',
      pointerEvents: 'none',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
    },
    inputRoot: {
      color: 'inherit',
    },
    inputInput: {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
      transition: theme.transitions.create('width'),
      width: '100%',
      [theme.breakpoints.up('sm')]: {
        width: '12ch',
        '&:focus': {
          width: '20ch',
        },
      },
    },
  }));
  

function Navbar() {
    const classes = useStyles();
    const[sidebar, setSidebar]=useState(false)
    const showSidebar=()=>setSidebar(!sidebar)
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);
    const [auth, setAuth] = React.useState(true);
    const handleChange = (event) => {
      setAuth(event.target.checked);
    };
  
    const handleMenu = (event) => {
      setAnchorEl(event.currentTarget);
    };
  
    const handleClose = () => {
      setAnchorEl(null);
    };
  
    return (
        <>
              <IconContext.Provider value={{color:'#fff'}}>  
             <div className="navbar">
               
                <Link to="#" className="menu-bars">
                    <FaIcons.FaBars onClick={showSidebar}/> 
                    
                    {/* <PrimarySearchAppBar/> */}
                </Link>
                <Link to="#" className="menu-bars">
                <div className={classes.search}>
            <div className={classes.searchIcon}>
            <MatIcons.Search />
            </div>
            <Material.InputBase
              placeholder="Search‚Ä¶"
              classes={{
                root: classes.inputRoot,
                input: classes.inputInput,
              }}
              inputProps={{ 'aria-label': 'search' }}
            />
          </div>
                </Link>
                <Link to="#" className="menu-bars">
                {auth && (
                <div>
              <Material.IconButton
              className={classes.menuButton}
                aria-label="account of current user"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleMenu}
                color="inherit"
              >
                <MatIcons.AccountCircle />
              </Material.IconButton>
              <Material.Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={handleClose}
              >
                <Material.MenuItem onClick={handleClose}>Profile</Material.MenuItem>
                <Material.MenuItem onClick={handleClose}>My account</Material.MenuItem>
              </Material.Menu>
            </div>
             )}
                </Link>
        </div>
                <nav className={sidebar?'nav-menu active':'nav-menu'}>
                    <ul className="nav-menu-items" onClick={showSidebar} >
                        <li className="navbar-toggle">
                            <Link to="#" className="menu-bars">
                               <AiIcons.AiOutlineClose/>
                            </Link>
                        </li>
                        {NavabarData.map((item,index)=>{
                            return (
                                <li key={index} className={item.cName}>
                                    <Link to={item.path}>
                                        {item.icon}
                                        <span>{item.title}</span>
                                    </Link>
                                </li>
                            )
                        })}
                    </ul>
                </nav>
                </IconContext.Provider>

        </>
    )
}

export default Navbar
